import { Component, OnInit } from '@angular/core';
import {MdSnackBar} from "@angular/material";
import {Store} from "@ngrx/store";
// import {Services} from "../../services/services";
import {ActivatedRoute, Router} from "@angular/router";
import {go} from "@ngrx/router-store";
import {LocalStorageService} from "angular-2-local-storage";
import {ShipmentServices} from "../../services/shipment-services";
import {TrackerServices} from "../../services/tracker-services";




@Component({
  selector: 'app-tracker-shipment-status',
  templateUrl: './tracker-shipment-status.component.html',
  styleUrls: ['./tracker-shipment-status.component.css']
})
export class TrackerShipmentStatusComponent implements OnInit {
  public view ='';

  public vehicleNo:any;
  public isSpinner:boolean = false;
  public vehicleState:any;
  public isExpand:boolean = false;
  public isDisconnectDialog:boolean = false;
  public isStartTimeDisable:boolean = false;
  public isEditTripLocationDialog:boolean = false;
  public movementLogs:any = {};
  public tripView = "list";
  public forConfirmPlace = "";
  public validationErrorMgs = "";
  public upSideHeight = '0px';
  public unSub_vehicleTripDetails = null;
  //public selectedShipment : Object ={};
  //public vehicleTripDetailsClone : Object ={};
  public selectedConfirmLocation : Object ={};
  public startTime:any=null;
  public tillTime:any=null;
  public tillTimeCheck =null;
  public forEditPlace ='';
  public unSub_customerDetails =null;
  public unSub_closeDialog =null;
  public distanceToGo : any;
  public eta : any;
  public timeValue : any;
  public isDelayed = false;
  public delayedBy : any;

  //*** */
  //public vehicleState:any='';
  public vehicleStatus:any='';
  public unSub_shipmentDetail= null;
  public isExtended :boolean = false;
  public selectedShipment:any={};
  public currentShipment :any = {};
  public selectedShipmentClone :any = {};
  
  public movLegLength:any;
  //public tillTime:any;
  //*** */

  ngAfterViewChecked(){
   let ref=document.getElementById("expandable-box");
    if(ref !=null) {
      let mapOffset = ref.getBoundingClientRect();
      // console.log(mapOffset);
      this.upSideHeight = ((0 - (mapOffset.height + 150)) + 'px');
      // console.log(this.upSideHeight)
    };
  }

  ngOnInit() {

  }

  ngOnDestroy() {

     
    this._store.dispatch({type: "IS_BACK",payload:{isBack:false,backToComponent:''}});
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined){
      this.unSub_customerDetails.unsubscribe();
    }

    if (typeof this.unSub_vehicleTripDetails != 'undefined' && this.unSub_vehicleTripDetails != null && this.unSub_vehicleTripDetails != undefined)
      this.unSub_vehicleTripDetails.unsubscribe();

    if (typeof this.unSub_closeDialog != 'undefined' && this.unSub_closeDialog != null && this.unSub_closeDialog != undefined)
      this.unSub_closeDialog.unsubscribe();
  }

  constructor(private _router:Router, private localStorageService:LocalStorageService,public snackBar: MdSnackBar,private _store:Store<any>,private shipmentServices:ShipmentServices,private trackerService:TrackerServices,private route: ActivatedRoute) {
    this.view ='web';
     console.log(this.view);

    //********************** get trip details data *****************
    this.unSub_shipmentDetail=this._store.select('shipmentDetail').subscribe(value => {
      console.log(value);
      if(value == null){
        return;
      }
      this.isSpinner = false;
      //this.selectedShipment=value['data'];
      this.selectedShipment = value['res']; // create new object
      console.log(this.selectedShipment);
      this.selectedShipmentClone =value['res']; // create new object
      console.log(this.selectedShipmentClone);
      if(this.selectedShipment!= null){
        if(this.selectedShipment['vts']!= null) {
          this.selectedShipment['vts']['_stateDuration'] = this.msToTime(this.selectedShipment['vts']['endTime'] - this.selectedShipment['vts']['startTime']);
          if (this.selectedShipment['currentShipment'] != null) {
            this.selectedShipment['currentShipment']['_transiteTime'] = this.msToTime(this.selectedShipment['currentShipment']['planedEndTime'] - this.selectedShipment['currentShipment']['planedStartTime']);
            console.log(this.selectedShipment['currentShipment']['_transiteTime']);
          }
          if (this.selectedShipment['vts']['state'] == 'EnrouteForPickUp') {
            this.vehicleState = 'Enroute for Pickup';
          }
          else if (this.selectedShipment['vts']['state'] == 'AtPickUpPoint') {
            this.vehicleState = 'At Pickup Point';
          }
          else if (this.selectedShipment['vts']['state'] == 'EnrouteForDestination') {
            this.vehicleState = 'Enroute for Delivery';

            if (this.selectedShipment['currentShipment'] != null) {
              this.selectedShipment['currentShipment'].startLocation['_loadingDuration'] = this.msToTime(this.selectedShipment['currentShipment'].startLocation['tillTime'] - this.selectedShipment['currentShipment'].startLocation['fromTime']);
            }

            console.log(this.selectedShipment['movementLegs']);
            if (this.selectedShipment['movementLegs'] != null) {
              let totalTravlingDistance = 0;
              let totalTravlingTime = 0;
              let stopCount = 0;
              let disconnectedCount = 0;
              for (let i = 0; i < this.selectedShipment['movementLegs'].length; i++) {
                totalTravlingDistance = totalTravlingDistance + this.selectedShipment['movementLegs'][i]['totalDistance'];
                totalTravlingTime = totalTravlingTime + this.selectedShipment['movementLegs'][i]['totalTime'];
                this.selectedShipment['movementLegs'][i]['_totalDuration'] = this.msToTime(this.selectedShipment['movementLegs'][i]['totalTime'])

                if (this.selectedShipment['movementLegs'][i]['state'] == 'Stopped') { // count total stopage
                  stopCount++;
                }
                if (this.selectedShipment['movementLegs'][i]['state'] == 'Disconnected') { // count total disconnection
                  disconnectedCount++;
                }
              }
              this.movementLogs.totalTravlingDistance = totalTravlingDistance;
              this.movementLogs._totalTravlingDistance = totalTravlingDistance / 1000;
              this.movementLogs.totalTravlingTime = totalTravlingTime;
              this.movementLogs._totalTravlingTime = this.msToTime(totalTravlingTime);
              this.movementLogs.disconnected = disconnectedCount;
              this.movementLogs.stopped = stopCount;

              console.log(this.movementLogs);
            }
          }
        
          
          else if (this.selectedShipment['vts']['state'] == 'AtDestination') {
            this.vehicleState = 'At Delivery Point';

            if (this.selectedShipment['currentShipment'] != null) {
              this.selectedShipment['currentShipment'].startLocation['_loadingDuration'] = this.msToTime(this.selectedShipment['currentShipment'].startLocation['tillTime'] - this.selectedShipment['currentShipment'].startLocation['fromTime']);
              this.tillTimeCheck=this.selectedShipment['currentShipment'].endLocation['tillTime'];
            }

            if (this.selectedShipment['movementLegs'] != null) {
              let totalTravlingDistance = 0;
              let totalTravlingTime = 0;
              let stopCount = 0;
              let disconnectedCount = 0;
              for (let i = 0; i < this.selectedShipment['movementLegs'].length; i++) {
                totalTravlingDistance = totalTravlingDistance + this.selectedShipment['movementLegs'][i]['totalDistance'];
                totalTravlingTime = totalTravlingTime + this.selectedShipment['movementLegs'][i]['totalTime'];
                this.selectedShipment['movementLegs'][i]['_totalDuration'] = this.msToTime(this.selectedShipment['movementLegs'][i]['totalTime'])

                if (this.selectedShipment['movementLegs'][i]['state'] == 'Stopped') { // count total stopage
                  stopCount++;
                }
                if (this.selectedShipment['movementLegs'][i]['state'] == 'Disconnected') { // count total disconnection
                  disconnectedCount++;
                }
              }
              this.movementLogs.totalTravlingDistance = totalTravlingDistance;
              this.movementLogs._totalTravlingDistance = totalTravlingDistance / 1000;
              this.movementLogs.totalTravlingTime = totalTravlingTime;
              this.movementLogs._totalTravlingTime = this.msToTime(totalTravlingTime);
              this.movementLogs.disconnected = disconnectedCount;
              this.movementLogs.stopped = stopCount;
              console.log(this.movementLogs);
            }
          }
          console.log(this.selectedShipment);
        }
      }
    });



    this.unSub_closeDialog = _store.select('closeDialog').subscribe(value => {
      console.log(value);
      this.isEditTripLocationDialog = value['isClose'];
    });

      // console.log(currentTripVehicle);
  } // constructor close

  // back(){
  //   this._store.dispatch(go(['dashboard/trip/status']));
  // }

  goToEditTripPickupView(forEditPlace,tripName){
    let obj = {'selectedShipment':this.selectedShipmentClone,'forEditPlace':forEditPlace,'tripName':tripName};
    this.localStorageService.set("SHIPMENT-TRIP-DETAIL-OBJ",obj);

    this.forEditPlace  =forEditPlace;
    this.isEditTripLocationDialog =true;
    // if(this.view == 'web'){
    //   this.forEditPlace  =forEditPlace;
    // this.isEditTripLocationDialog =true;
    // }
    // else{
    //   this._store.dispatch(go(['/vtsdashboard/trip/status/details/editTripLocation']));
    // }

  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }

    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime

  openConfirmPointDialog(forConfirmPlace){
    this.forConfirmPlace = forConfirmPlace;
    this.validationErrorMgs = "";
    console.log(forConfirmPlace);
    this.isDisconnectDialog = true;
    console.log( this.selectedShipment);

    if(this.selectedShipment['vts']['state'] == 'EnrouteForDestination' || this.selectedShipment['vts']['state'] == 'AtDestination'){
        // this.selectedConfirmLocation = this.selectedShipment ['currentShipment']['destination'][0];
        if(this.selectedShipment ['currentShipment']['endLocation']['fromTime']!=null)
      this.startTime = new Date(this.selectedShipment ['currentShipment']['endLocation']['fromTime']);
      if(this.selectedShipment ['currentShipment']['endLocation']['tillTime'])
      this.tillTime =new Date( this.selectedShipment ['currentShipment']['endLocation']['tillTime']);
    } else if(this.selectedShipment['vts']['state'] == 'EnrouteForPickUp' || this.selectedShipment['vts']['state'] == 'AtPickUpPoint' ){
      // this.selectedConfirmLocation = this.selectedShipment ['currentShipment']['origin'][0];
      if(this.selectedShipment ['currentShipment']['startLocation']['fromTime']!=null)
      this.startTime =new Date(this.selectedShipment ['currentShipment']['startLocation']['fromTime']);
      console.log(this.startTime);
      if(this.selectedShipment ['currentShipment']['startLocation']['tillTime']!=null)
      this.tillTime = new Date(this.selectedShipment ['currentShipment']['startLocation']['tillTime']);
    }

    if(this.selectedShipment['vts']['state'] == 'EnrouteForDestination' || this.selectedShipment['vts']['state'] == 'EnrouteForPickUp' ){
      this.isStartTimeDisable = false;
      this.startTime = null;
      this.tillTime = null;
    }
    else if(this.selectedShipment['vts']['state'] == 'AtDestination' || this.selectedShipment['vts']['state'] == 'AtPickUpPoint' ){
      this.isStartTimeDisable = true;
    }
  }

  confirmPlace(){
   if(this.checkValid()) {
     let eTime = null;
     if (this.tillTime != null) {
       eTime = new Date(this.tillTime).getTime();
       console.log(eTime);

       //@todo more set validation
       if (eTime < new Date(this.startTime).getTime()) {
         // console.log("start Time should not be greater then end Time");
         this.validationErrorMgs = "start Time should not be greater then end Time";
         return;
       }

       let todayDate = new Date().getTime();
       if (eTime > todayDate) {
         // console.log("start Time should not be greater then end Time");
         this.validationErrorMgs = "End time should not be greater then current date";
         return;
       }
     }

     this.isDisconnectDialog = false;
     let trackingType:any;
     if(this.selectedShipment['currentShipment']['trackingType']=='Tracker'){
       trackingType='tracker';
     }
     else
       trackingType='trip';

       console.log(this.startTime);
     let obj = {forConfirmPlace:this.forConfirmPlace,
       conObj: {
         vehicleId: this.selectedShipment['currentShipment']['vehicleId'],
         deviceImei: this.selectedShipment['currentShipment']['deviceImei'],
         tripId: this.selectedShipment['vts']['tripId'],
         trackingType:trackingType,
         startTime: new Date(this.startTime).getTime(),
         endTime: eTime
       }};

     console.log(obj);
     this.trackerService.confirmLocation.emit(obj);// after call it page data automatically  update by socket services.
     this.isSpinner=true;
     setTimeout(()=>{
       this.shipmentServices.getShipmentDetail.emit(this.selectedShipment.currentShipment.shipmentId);
     },1000);
    }
  }

  checkValid(){
    if(this.isStartTimeDisable){
      console.log('in validate'+this.startTime);
      console.log(this.tillTime);
      
      if(this.startTime == null ){
        this.validationErrorMgs = "Start Time should not be blank";
        return false;
      }
      if(this.tillTime == null){
        this.validationErrorMgs = "End Time should not be blank";
        return false;
      }

      let todayDate = new Date().getTime();
      if (new Date(this.startTime).getTime() > todayDate) {
        this.validationErrorMgs = "start time should not be greater then current date";
        return false;
      }

    }else{
      if(this.startTime == null ){
        this.validationErrorMgs = "start time should not be blank";
        return false;
      }

      let todayDate = new Date().getTime();
      if (new Date(this.startTime).getTime() > todayDate) {
        this.validationErrorMgs = "start time should not be greater then current date";
        return false;
      }
    }
    return true;
  }// close ckeck valid

}



