import { Component, OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {MdSnackBar, MdSnackBarConfig} from "@angular/material";
import {UserManagementService} from "../../../services/user-management-services";
import {LocalStorageService} from "angular-2-local-storage";


@Component({
  selector: 'app-users-list',
  templateUrl: './users-list.component.html',
  styleUrls: ['./users-list.component.css']
})
export class UsersListComponent implements OnInit {

  // public view ='';
  public emailId:string ='';
  public isSpinner =false;
  public mobileNo:string ='';
  public existingUserName:string ='';
  public existingUserMobile:string ='';
  public existingUserEmail:string ='';
  public token:any;
  public existingUserObj:any={};

  public selectedUserForDelete:any={};
  public orgId:string ='';
  public isShowInviteUser:boolean=false;
  public isShowUserDetails:boolean=false;
  public isShowDeleteUserConfirmation:boolean=false;
  public isShowExistingUserDetails:boolean=false;

  public allUsersList:any=[];

  private unSub_UsersList: any =null;
  private unSub_DeleteUserResponse: any =null;
  private unSub_UpdateUserResponse: any =null;
  private unSub_getAddExistingUser: any =null;
  private unSub_customerDetails: any =null;
  constructor(private _store:Store<any>, public userMgmntService:UserManagementService,public snackBar: MdSnackBar, public _localStorageServices: LocalStorageService) {

    this.allUsersList=[];


    let _authToken=this._localStorageServices.get("AUTH_TOKEN");
    this.token=this._localStorageServices.get("AUTH_TOKEN");
    // this.token=_authToken['token'];

    console.log("this.token");
    console.log(this.token);

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      this.allUsersList=[];
      if(value==null){
        return;
      }

      this.orgId=value['customer']['orgId'];
      console.log(this.orgId);
      this.isSpinner =true;
      this.userMgmntService.getAllUsersList.emit(this.token);
    });


    this.unSub_UsersList = _store.select('getAllUserList').subscribe(value => {
      this.allUsersList=[];
      if(value==null) {
        return;
      }
      console.log('all users list in component');
      console.log(value);
      this.isSpinner =false;
      this.allUsersList=value;
    });

    this.unSub_getAddExistingUser = _store.select('existingUserResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      if(value['status'] == 400)
      {
        this.isShowUserDetails=true;
        this.isShowExistingUserDetails=false;
        return;
      }

      console.log('existing users details in component');
      console.log(value['status']);

        this.isShowExistingUserDetails=true;

        this.existingUserObj=value;
        this.existingUserName=value['name'];
        this.existingUserEmail=value['email'];
        this.existingUserMobile=value['mobileNumber'];
        // this.existingUserName=

      this.refreshListAfterInterval();
    });

    this.unSub_DeleteUserResponse = _store.select('deleteUserResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('all users list in component');
      console.log(value);

      this.openSnackBar("User Deleted Successfully !");
      this.userMgmntService.getAllUsersList.emit(this.token);
      this.refreshListAfterInterval();
    });

    this.unSub_UpdateUserResponse = _store.select('updateUserStatusResponse').subscribe(value => {

      if(value==null)
      {
        return;
      }
      console.log('all users list in component');
      console.log(value);

      this.openSnackBar("User Status Updated !");
      this.isShowUserDetails=false;
      this.userMgmntService.getAllUsersList.emit(this.token);
      this.refreshListAfterInterval();
    });

  }

  ngOnInit() {
  }


  ngOnDestroy()
  {
    if (typeof this.unSub_UsersList != 'undefined' && this.unSub_UsersList != null && this.unSub_UsersList != undefined)
      this.unSub_UsersList.unsubscribe();

    if (typeof this.unSub_UsersList != 'undefined' && this.unSub_UsersList != null && this.unSub_UsersList != undefined)
      this.unSub_UsersList.unsubscribe();

    if (typeof this.unSub_UpdateUserResponse != 'undefined' && this.unSub_UpdateUserResponse != null && this.unSub_UpdateUserResponse != undefined)
      this.unSub_UpdateUserResponse.unsubscribe();

    if (typeof this.unSub_getAddExistingUser != 'undefined' && this.unSub_getAddExistingUser != null && this.unSub_getAddExistingUser != undefined)
      this.unSub_getAddExistingUser.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

  }

  showInviteUser()
  {
    this.isShowInviteUser=true;
  }

  confirmAddUser()
  {
    this.isShowInviteUser=false;
    this.isShowUserDetails=true;
    let obj={
      email: this.emailId,
      mobile: this.mobileNo
    }

    this.userMgmntService.getUserIfExist.emit(obj);
  }

  addUser()
  {
    let obj={
      "userUuid":this.existingUserObj['uuid'],
      "orgUuid":this.orgId,
      "accessLevel":"user"
    }
    console.log(obj);
    this.userMgmntService.addUserToGroup.emit(obj);
  }

  deleteUserConfirmationBox(selectedUser)
  {
    console.log("Delete user ??");
      console.log(selectedUser);
      let obj = {
        "userUuid": selectedUser['user']['uuid'],
        "orgUuid": this.orgId,
        "accessLevel": selectedUser['accessLevel']
      }
      this.selectedUserForDelete = obj;

      this.isShowDeleteUserConfirmation = true;

  }


  deleteSelecetdUser()
  {
    if(this.allUsersList.length > 1) {
      this.isShowDeleteUserConfirmation = false;
      this.userMgmntService.deleteUser.emit(this.selectedUserForDelete);
    }
    else{
      this.openSnackBar("Atleast one user is required.");
    }
  }

  changeUserStatus(userObj)
  {
    let acl="";
    if(userObj['accessLevel']== 'user')
    {
      acl='admin';
    }
    if(userObj['accessLevel']== 'admin')
    {
      acl='user';
    }
    let obj={
      "userUuid":userObj['user']['uuid'],
      "orgUuid":this.orgId,
      "accessLevel":acl
    }
    this.userMgmntService.changeUserType.emit(obj);
  }

  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }


  refreshListAfterInterval()
  {
    setTimeout(() =>{
      this.userMgmntService.getAllUsersList.emit(this.token);
    },3000);

    // setTimeout(() =>{
    //   this.userMgmntService.getAllUsersList.emit(this.token);
    // },5000);

    // setTimeout(() =>{
    //   this.userMgmntService.getAllUsersList.emit(this.token);
    // },10000);
  }

}



