import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'orderBy', pure: false})
export class SortFilter {

  transform(input:any[], config:any): any {
    input.sort((a: any, b: any) => {
        if (a[config] < b[config]){
            return -1;
        }else if( a[config] > b[config] ){
            return 1;
        }else{
            return 0;
        }
    });
    return input;
  }
}


