import { Component, OnInit } from '@angular/core';
import { MdDialogRef} from '@angular/material';
import {Store} from "@ngrx/store";
@Component({
  selector: 'app-confirm-device-return',
  templateUrl: './confirm-device-return.component.html',
  styleUrls: ['./confirm-device-return.component.css']
})
export class ConfirmDeviceReturnComponent implements OnInit{
  public deviceCondition:any[]=[{viewName:'Working',value:"working"},
                                {viewName:'Notworking',value:"notworking"}];
  public deviceStatus :any ='working';
  public device:any;
  public unSub_deviceDetail:any=null;
  constructor(public dialogRef: MdDialogRef<ConfirmDeviceReturnComponent>,private _store:Store<any>)
    {
      this.unSub_deviceDetail=this._store.select('deviceDetails').subscribe(value => {
        if(value!=null){
          if(value['error']==null) {
            //for Error Handaling
          }
          else
          {
            this.device = value['data'];
            console.log(this.device);
          }
        }
      });
    }

    ngOnInit() {
    }
    ngOnDestroy() {
      if (typeof this.unSub_deviceDetail!= 'undefined' && this.unSub_deviceDetail != null && this.unSub_deviceDetail != undefined)
      this.unSub_deviceDetail.unsubscribe();

    }
    // @Inject(MD_DIALOG_DATA) public data: any) { }

  onNoClick(): void {
    this.dialogRef.close();
  }

  closeDialog() {
    this.dialogRef.close();
  }

  confirmDeviceReturnDialog(){
    this.dialogRef.close('Pizza! bjhnm');
  }
}



