import { Component ,OnInit } from '@angular/core';
import {Store} from "@ngrx/store";
import {MdDialog, MdDialogRef, MD_DIALOG_DATA} from '@angular/material';

@Component({
  selector: 'app-mark-as-required',
  templateUrl: './mark-as-required.component.html',
  styleUrls: ['./mark-as-required.component.css']
})
export class MarkAsRequiredComponent implements OnInit {
    public deviceCondition:any[]=[{viewName:'Repaired',value:"repaired"},
                                  {viewName:'Damaged',value:"damaged"},
                                  {viewName:'Sent For Replacment',value:"replacment"}];  
    public deviceStatus :any ='repaired';
    public device:any;
    public unSub_deviceDetail:any=null;
    constructor(public dialogRef: MdDialogRef<MarkAsRequiredComponent>,private _store:Store<any>)
      {
        this.unSub_deviceDetail=this._store.select('deviceDetails').subscribe(value => {
          if(value!=null){
            if(value['error']==null) {
              //for Error Handaling          
            }
            else
            {
              this.device = value['data'];
              console.log(this.device);
            }
          }
        });
      }
  
    ngOnInit() {
    }
    ngOnDestroy() {
      if (typeof this.unSub_deviceDetail != 'undefined' && this.unSub_deviceDetail != null && this.unSub_deviceDetail != undefined)
      this.unSub_deviceDetail.unsubscribe();
    
    }
    onNoClick(): void {
      this.dialogRef.close();
    }
    closeDialog() {
      this.dialogRef.close('Pizza!');
    }
  
  }


