import {Pipe, PipeTransform} from "@angular/core";

@Pipe({ name : "deviceType",pure: true})
export class DeviceFilter implements  PipeTransform{
    //TODO *****************************
    transform(input:any[], config:any):any {
        if (config === undefined || config[1] == undefined || config[0] == undefined ) return [];
        if (!Array.isArray(input)) return input;

        if(config[1]=='')
        {
        return input;
        }
        console.log(config);
        console.log(config);
        var list = input.filter(u => u['device'][config[0]].toUpperCase().indexOf(config[1].toUpperCase()) != -1 );
        {
        return list ;
        }
    }
 }



