import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'searchFilter', pure: true})
export class SearchFilter implements PipeTransform {
  transform(input:any[], config:any):any {
    if (config === undefined || config[2] === undefined || config[1] == undefined || config[0] == undefined) return [];
    if (!Array.isArray(input)) return input;

    if(config[2]=='')
    {
      return input;
    }
    console.log(config);

    var list =  input.filter(u => (""+u[config[1]]).toUpperCase().indexOf((""+config[2]).toUpperCase()) != -1 || (""+u[config[0]]).toUpperCase().indexOf((""+config[2]).toUpperCase()) != -1);
    {
      console.log(list);
      return list ;
    }
  }
}





