import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
// import {PATH,DETAIL,CREATESHIPMENT,ADDSTOP,PICKUP,DELIVERY} from "./path-services";
import { paths } from "../../environments/environment";
import { LocalStorageService } from "angular-2-local-storage";

// import {KEY} from "./config-services"

@Injectable()
export class ShipmentServices {
  public defaultHeaders: Headers = new Headers();
  private token:any='';
   
  public getShipmentList=new EventEmitter();
  public getShipmentDetail=new EventEmitter();
  public createNewShipment=new EventEmitter();
  public checkVehicle=new EventEmitter();
  // public checkDevice=new EventEmitter();
  
  public switchShipmentDetail=new EventEmitter();
  
  
   
  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorgeService:LocalStorageService){
     /***************get shipment list ***************/
     this.token=this.localStorgeService.get("AUTH_TOKEN");
    this.getShipmentList.subscribe(( )=> {
      let path = paths.SHIPMENT_PATH+'/dashboard';
      console.log(path);
      this.getList(path).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status < 300){
          this._store.dispatch({type: "All_SHIPMENT_LIST", payload: {error:'',res:response['list']}});
        }else{
          this._store.dispatch({type: "All_SHIPMENT_LIST", payload: {error:'X',errorMsg:'server error...'}});
        }

      }, err => {
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "All_SHIPMENT_LIST", payload: {error:'X',errorMsg:"error occur "+err}});
      });
    });
    /************* get shipment details  *************/
    this.getShipmentDetail.subscribe((id)=> {
      let path = paths.SHIPMENT_PATH+'/detail/'+id;
      console.log(path);
      this.getDetails(path).subscribe(response =>{
        console.log(response['detail']);
        if(response.status >=200 && response.status < 300){
          this._store.dispatch({type: "SHIPMENT_DETAIL", payload: {error:'',res:response['detail']}});
        }else{
          this._store.dispatch({type: "SHIPMENT_DETAIL", payload: {error:'X',errorMsg:'server error...'}});
        }

      }, err => {
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "SHIPMENT_DETAIL", payload: {error:'X',errorMsg:"error occur "+err}});
      });
    });
 
    /************* create shipment   *************/
    this.createNewShipment.subscribe((shipment)=> {
      let path =paths.SHIPMENT_PATH+'/create';
      console.log(path);
      this.createShipment(path,shipment).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status < 300){
          this._store.dispatch({type: "NEW_SHIPMENT", payload: {error:'',res:response}});
          setTimeout(()=>{
            this.getShipmentList.emit();
          },1500);
        }else{
          this._store.dispatch({type: "NEW_SHIPMENT", payload: {error:'X',errorMsg:'server error...'}});
        }
      },err=>{
        console.log(err.json());
        this._store.dispatch({type: "NEW_SHIPMENT", payload: {error:'X',errorMsg:"error occur "+err}});
        
      });
    });

    /************* get shipment details  *************/
    /************* switch shipment   *************/
    this.switchShipmentDetail.subscribe((shipment)=> {
      let path =paths.SHIPMENT_PATH+'/switch/trackmode';
      console.log(path);
      this.switchShipment(path,shipment).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status < 300){
          this._store.dispatch({type: "SWITCH_SHIPMENT", payload: {error:'',res:response}});
        }else{
          this._store.dispatch({type: "SWITCH_SHIPMENT", payload: {error:'X',errorMsg:'server error...'}});
        }
      },err=>{
        console.log(err.json());
        this._store.dispatch({type: "SWITCH_SHIPMENT", payload: {error:'X',errorMsg:"error occur "+err}});
        
      });
    });

    /*************check vehicle details  *************/
    this.checkVehicle.subscribe((id)=> {
      let path = paths.CHECK_VEHICLE+'?registrationNumber='+id;
      console.log(path);
      this.getVehicle(path).subscribe(response =>{
        console.log(response);
        if(response.status >=200 && response.status < 300){
          this._store.dispatch({type: "CHECK_VEHICLE", payload: {error:'',res:response }});
        }else{
          this._store.dispatch({type: "CHECK_VEHICLE", payload: {error:'X',errorMsg:'server error...'}});
        }

      }, err => {
        console.log(err);
        console.log(err.json());
        this._store.dispatch({type: "CHECK_VEHICLE", payload: {error:'X',errorMsg:"error occur "+err}});
      });
    });
    /*************check Device details  *************/
    // this.checkDevice.subscribe((id)=> {
    //   let path = paths.CHECK_DEVICE+'?imei='+id;
    //   console.log(path);
    //   this.getVehicle(path).subscribe(response =>{
    //     console.log(response);
    //     if(response.status >=200 && response.status < 300){
    //       this._store.dispatch({type: "CHECK_DEVICE", payload: {error:'',res:response }});
    //     }else{
    //       this._store.dispatch({type: "CHECK_DEVICE", payload: {error:'X',errorMsg:'server error...'}});
    //     }

    //   }, err => {
    //     console.log(err);
    //     console.log(err.json());
    //     this._store.dispatch({type: "CHECK_VEHICLE", payload: {error:'X',errorMsg:"error occur "+err}});
    //   });
    // });
 
  }// constructor close here
 
// ***********************  get shipment list **********************
  public getList(path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
    });

// console.log(this.http);
    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
        return response.json();
    });
  }

// ***********************  get vehicle list **********************
public getVehicle(path){
  let headers = new Headers(this.defaultHeaders.toJSON());
  headers.set('Content-Type', 'application/json');
  let bearerToken="Bearer "+this.token;
  headers.set('authorization', bearerToken);
  let requestOptions: RequestOptionsArgs = new RequestOptions({
    method: RequestMethod.Get,
    headers: headers,
  });

// console.log(this.http);
  return this.http.request(path, requestOptions).map((response: Response) => {
    console.log(response);
      return response.json();
  });
}
// ***********************  switch shipment **********************
public switchShipment(path,shipment){
  let headers = new Headers(this.defaultHeaders.toJSON());
  headers.set('Content-Type', 'application/json');
  let bearerToken="Bearer "+this.token;
  headers.set('authorization', bearerToken);
  let requestOptions: RequestOptionsArgs = new RequestOptions({
    method: RequestMethod.Post,
    headers: headers,
    body:shipment
  });

// console.log(this.http);
  return this.http.request(path, requestOptions).map((response: Response) => {
    console.log(response);
      return response.json();
  });
}


  // ****************** get Vehicle Detail *******************
  public getDetails(path){
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers:headers,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);
        return response.json();
    });
  }

  // ****************** create Shipment *******************
  public createShipment(path,shipment){
    console.log(path);
    console.log(shipment);
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);
    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers:headers,
      body: shipment
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      console.log(response);

        return response.json();
    });
  }
  initialize(){
    this._store.dispatch({type: "NEW_SHIPMENT", payload: null});
  }
}// class close




