import { Component, OnInit, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import { MapsAPILoader, AgmMap, GoogleMapsAPIWrapper } from "@agm/core";
import * as mapTypes from '@agm/core/services/google-maps-types';
import { } from '@types/googlemaps';
import poly = google.maps.geometry.poly;

@Component({
  selector: 'app-freight-location-creater',
  templateUrl: './freight-location-creater.component.html',
  styleUrls: ['./freight-location-creater.component.css']
})
export class FreightLocationCreaterComponent implements OnInit {

  @ViewChild(AgmMap)
  agmMap;

  public latitude: number = null;
  public longitude: number = null;
  public zoom: number = 5;
  public isMarkerDragable: boolean = false;
  public isPolygonEditable: boolean = false;
  public polygon: any;
  public map: any;
  public mapType = 'roadmap';
  public markers: any = [];

  public fretDetails: any;

  public name: string = "";
  public freightObj: any = {};

  public isVisible = false;
  public isSearched = false;
  public circle = null;

  public isCircle: boolean;
  public radius: Number;
  public address: string;

  // public dialogRef: any;
  public res: any;
  // public isDisable = true;
  private selectedValue:any ;
  public toAddProximity = true;
  public toAddBoundary = true;
  // public id: any;

  @Output() onLocationCreation = new EventEmitter<any>();
  @Input() selectedValueFromParent : any;

  constructor(public mapApiWrapper: GoogleMapsAPIWrapper) {
    this.latitude = null;
    this.longitude = null;
    console.log(this.latitude);
    this.zoom = 14;
    console.log("constructor");
  } // constructor close

  ngOnInit() {
    console.log("in on init");
    // if(this._calledFrom != null) {
    console.log("oninit");
    this.agmMap.mapReady.subscribe(map => {
      this.map = map;
      // let points = this.getNPointsCircularPolygone({lat : this.latitude , lng : this.longitude} , 0.5 , 6);
      let points = [
        // {lat: 21.15083373, lng: 79.084234},
        // {lat: 21.1537726, lng: 79.09427709},
        // {lat: 21.1466711482, lng: 79.08886976}
      ];

      console.log(points);
      this.selectedValue = this.selectedValueFromParent
      console.log(this.selectedValueFromParent);
      console.log(this.selectedValue);

      if(this.selectedValue['type']=="Proximity") {
      this.toAddProximity = true;
      this.toAddBoundary = false;
      // this.selectedValue = "both";
      }

      else if(this.selectedValue['type']=="Polygon") {
      this.toAddBoundary = true;
      this.toAddProximity = false;
      // this.selectedValue = "both";
      }

      else {
        this.toAddProximity = true;
        this.toAddBoundary = true;
      }

      // this.selectedValue['type'] = "both";
      this.polygon = this.addPolygone(this.map, points, false, '#858585', '#353535');
      this.editLocation();
    });
    // }
  }

  ngOnDestroy() {
    // this.selectedValue['type'] = "both";
  }

  addPolygone(map, points, editable, fillColor, strokeColor) {
    // Map Center
    // Styling & Controls
    let myPolygon = new google.maps.Polygon({
      paths: points,
      // draggable: true, // turn off if it gets annoying
      editable: editable,
      strokeColor: strokeColor,
      // strokeColor: '#353535',
      strokeOpacity: 0.8,
      strokeWeight: 2,
      // fillColor: '#858585',
      fillColor: fillColor,
      fillOpacity: 0.35
    });
    myPolygon.setMap(map);
    return myPolygon;
  }

  getNPointsCircularPolygone(center, dist, n) {
    var i = 0;
    var polygonePoint = [];
    for (i = 1; i <= n; i++) {
      polygonePoint.push(getPointAt(center, (360.0 / n) * (i), dist))
    }

    function toRAD(NUM) {
      return NUM * Math.PI / 180;
    }

    function toDeg(NUM) {
      return NUM * 180 / Math.PI;
    }

    function getPointAt(latlng, brng, dist) {
      // console.log(latlng);
      // console.log(latlng);
      dist = dist / 6371;
      brng = toRAD(brng);

      var lat1 = toRAD(latlng.latitude), lon1 = toRAD(latlng.longitude);
      var lat2 = Math.asin(Math.sin(lat1) * Math.cos(dist) + Math.cos(lat1) * Math.sin(dist) * Math.cos(brng));
      var lon2 = lon1 + Math.atan2(Math.sin(brng) * Math.sin(dist) * Math.cos(lat1), Math.cos(dist) - Math.sin(lat1) * Math.sin(lat2));

      if (isNaN(lat2) || isNaN(lon2)) return null;
      return new google.maps.LatLng(toDeg(lat2), toDeg(lon2))
    };
    return polygonePoint;
  }

  editLocation() {
    // this.isMarkerDragable=true;
    // this.polygon.setMap(null);//

    this.isPolygonEditable = true;
    this.polygon.setEditable(true);
    this.polygon.setOptions({ fillColor: '#FF0000', strokeColor: '#FF0000' });
    this.placesServices();
  }

  back() {

  }


  placesServices() {
    // Create the search box and link it to the UI element.
    let input: any = document.getElementById('pac-input');
    console.log(input);
    let searchBox = new google.maps.places.SearchBox(input);
    this.map.addListener('bounds_changed', () => {
      searchBox.setBounds(this.map.getBounds());
    });

    this.markers = [];

    if (this.res != null) {
      if (this.res["data"]["radius"] == null) {
        // console.log(this.res["data"]["radius"])
        let points = this.getNPointsCircularPolygone({ latitude: this.latitude, longitude: this.longitude }, 0.5, 5);
        this.polygon.setPath(points);
        this.polygon.setMap(this.map);
      }
      else {
        this.drawCircel((Number)(this.res["data"]["radius"]));
      }
    }
    searchBox.addListener('places_changed', () => {
      var places = searchBox.getPlaces();

      if (places.length == 0) {
        return;
      }

      // Clear out the old markers.
      this.markers.forEach((marker) => {
        marker.setMap(null);
      });
      this.markers = [];

      // For each place, get the icon, name and location.
      let bounds = new google.maps.LatLngBounds();
      places.forEach((place) => {
        if (!place.geometry) {
          console.log("Returned place contains no geometry");
          return;
        }
        let icon = {
          url: place.icon,
          size: new google.maps.Size(71, 71),
          origin: new google.maps.Point(0, 0),
          anchor: new google.maps.Point(17, 34),
          scaledSize: new google.maps.Size(25, 25)
        };


        // Create a marker for each place.
        let currentMarker = new google.maps.Marker({
          // map: this.map,
          // icon: icon,
          title: place.formatted_address,
          position: place.geometry.location
        });

        // console.log(place.geometry.location);
        this.latitude = currentMarker['position'].lat();
        this.longitude = currentMarker['position'].lng();

        this.isSearched = true;
        console.log(this.latitude + " " + this.longitude);

        this.address = place.formatted_address;

        //  this.polygon.setMap(this.map);
        console.log(place.formatted_address);
        this.markers.push(currentMarker);


        // ********** for show marker according to screen ********
        if (place.geometry.viewport) {
          // Only geocodes have viewport.
          bounds.union(place.geometry.viewport);
        } else {
          bounds.extend(place.geometry.location);
        }
        // ****** for show marker accoeding to screen  ************
      });
      this.map.fitBounds(bounds);
    });
  }

  openSnackBar(message: string) {
    // this.snackBar.open(message, '', <MdSnackBarConfig>{ duration: 4000 });
  }

  onAddProximity(): void {
    this.isVisible = true;
    this.isCircle = true;
    // circle.bindTo('center',marker,'position');
  }


  // method to draw circle

  drawCircel(radius) {
    // this.radius = radius;
    this.isCircle = true;
    if (this.circle != null)
      this.circle.setMap(null);

    if (this.polygon != null)
      this.polygon.setMap(null);

    this.circle = new google.maps.Circle({
      radius: radius,
      center: new google.maps.LatLng(this.latitude, this.longitude)
    })


    this.circle.setMap(this.map)
  }


  // Add location boundary on map
  onAddLocation(): void {
    this.radius = null;

    this.isCircle = false;
    this.isVisible = false;
    let obj = { latitude: this.latitude, longitude: this.longitude };
    console.log('Set New Location Obj');
    console.log(obj);

    if (this.circle != null)
      this.circle.setMap(null);

    if (this.polygon != null)
      this.polygon.setMap(null);

    let points = this.getNPointsCircularPolygone({ latitude: this.latitude, longitude: this.longitude }, 0.5, 5);
    this.polygon.setPath(points);
    this.polygon.setMap(this.map);

    this.map.setZoom(14);

    console.log(this.fretDetails);
  }

  onKeyEvent(event): void {
    if ((event.keyCode >= 48 && event.keyCode <= 57) || (event.keyCode >= 96 && event.keyCode <= 105) || event.keyCode == 8) {
      this.drawCircel(Number(this.radius));
    }
  }

  saveLocation(): void {
    if (this.radius == null) {
      let vertices = this.polygon.getPath();
      let polygonPoints:any[]=[];
      // Iterate over the vertices.
      for (let i =0; i < vertices.getLength(); i++) {
        let xy = vertices.getAt(i);
        polygonPoints.push({latitude:xy.lat(), longitude:xy.lng()});
        console.log(xy.lat()+"     "+xy.lng());
      }

      this.freightObj = {
        "name": this.name,
        "address": this.address,
        "geofence":{
          "boundry":polygonPoints,
          "center": { "latitude": this.latitude, "longitude": this.longitude },
          "type": "Polygon"
        }

      }

      console.log(this.freightObj);
    }
    else {
      this.freightObj = {
        "name": this.name,
        "address": this.address,
        "geofence":{
          "type": "Proximity",
          "circle": {
            "center": { "latitude": this.latitude, "longitude": this.longitude },
            "radius": this.radius
          }

        }

      }
      console.log(this.freightObj);
    }

    let emiter = this.onLocationCreation.emit(this.freightObj);
    console.log(emiter);
  }
}



