import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
// import {TRACKER_TRIP_DISCARD_PATH,TRACKER_TRIP_UPDATE_PATH,TRACKER_TRIP_UPDATE_FROM_STOP_PATH,TRACKER_TRIP_CONFIRM_LOCATION_PATH } from "./path-services";
import { paths } from "../../environments/environment";

@Injectable()
export class TrackerServices {
  // protected basePath = 'http://35.189.162.187:8080/command';
  public defaultHeaders: Headers = new Headers();
  public discardTrackerTrip=new EventEmitter();
  public updateTrackerTrip=new EventEmitter();
  public updateTrackerTripFromStop=new EventEmitter();
  public confirmLocation=new EventEmitter();
  private token:any='';

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService){
    //@todo handle error cases
    this.token=this.localStorageService.get("AUTH_TOKEN");
    this.discardTrackerTrip.subscribe((discard)=> {
      let path:any;
      if(discard['forEditPlace'] == 'destination'){
        path =paths.SHIPMENT_PATH+"/discard/delivery";
      }else  if(discard['forEditPlace'] == 'pickup'){
        path =paths.SHIPMENT_PATH+"/discard/pickup";
      }
      console.log(path);
      console.log(discard['discardObj']);
      

      this.doDiscardTrip(path,discard['discardObj']).subscribe(response =>{
        // console.log(response);
      },err=>{
        console.log(err);
      });
    });

    //@todo handle error cases
    this.updateTrackerTrip.subscribe((updateTripObj)=> {
       let path =paths.SHIPMENT_PATH+'/update';
       console.log(path);
       console.log(updateTripObj);
       
      this.doUpdateTrip(path,updateTripObj).subscribe(response =>{
        // console.log(response);
      },err=>{
        console.log(err);
      });
    });

     //@todo handle error cases
    this.updateTrackerTripFromStop.subscribe((tripObj)=> {
      let path:any;
      if(tripObj['forEditPlace'] == 'destination'){
        path =paths.SHIPMENT_PATH+"/selectfromstop/delivery";
      }else  if(tripObj['forEditPlace'] == 'pickup'){
        path =paths.SHIPMENT_PATH+"/selectfromstop/pickup";
      }

      console.log(path);
      console.log(tripObj['movementObj']);

      this.doUpdateTripFromStop(path,tripObj['movementObj']).subscribe(response =>{
        console.log(response);
      },err=>{
        console.log(err);
      });
    });

    //@todo handle error cases
    this.confirmLocation.subscribe((obj)=> {
      let path:any;

      if(obj['forConfirmPlace'] == 'Destination'){
        path = paths.SHIPMENT_PATH+"/missing/delivery";
      }
      else  if(obj['forConfirmPlace'] == 'Pickup'){
        path = paths.SHIPMENT_PATH+"/missing/pickup";
      }

      console.log(path);
      console.log(obj['conObj']);
      this.doConfirmLocation(path,obj['conObj']).subscribe(response =>{
        console.log(response);
      },err=>{
        console.log(err);
      });
    });

  }// constructor close here
    

  

  // *********************** discard Trip  **********************
  public doDiscardTrip(path,discardTrip){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: discardTrip,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      // if(response.status==200){
      return response.json();
      // }else{
      //   console.log("not 200");
      //   return {error:'X'};
      // }
    });
  }


  // *********************** update trip  **********************
  public doUpdateTrip(path,updateTrip){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: updateTrip,
    });
    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      // if(response.status==200){
      return response.json();
      // }else{
      //   console.log("not 200");
      //   return {error:'X'};
      // }
    });
  }


  // *********************** update trip  from stop/list of movement logs  **********************
  public doUpdateTripFromStop(path,updateTrip){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: updateTrip,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      // if(response.status==200){
      return response.json();
      // }else{
      //   console.log("not 200");
      //   return {error:'X'};
      // }
    });
  }

  // *********************** update trip  from stop/list of movement logs  **********************
  public doConfirmLocation(path,obj){

    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+this.token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Post,
      headers: headers,
      body: obj,
    });

    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
      // if(response.status==200){
      return response.json();
      // }else{
      //   console.log("not 200");
      //   return {error:'X'};
      // }
    });
  }



}// class close




