import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {DashboardComponent} from "../components/dashboard/dashboard.component";
import {ShipmentStatusComponent} from "../components/shipment-status/shipment-status.component";
import {DeviceDashboard} from "../components/device-dashboard/device-dashboard.component";
import {NewShipment} from "../components/new-shipment/new-shipment.component";
import {ManualShipmentStatusComponent} from '../components/manual-shipment-status/manual-shipment-status.component'
import {AddOrganisationComponent} from "../components/add-organisation/add-organisation.component";
import {SettingsComponentComponent} from "../components/settings-component/settings-component.component";
import {ManageOrganisationComponent} from "../components/manage-organisation/manage-organisation.component";
// import {UserManagementComponent} from "../components/user-management/user-management.component";
import {ShipmentManagementComponent} from "../components/shipment-management/shipment-management.component";
// import {EditShipmentGroupsComponent} from "../components/shipment-management/edit-shipment-groups/edit-shipment-groups.component";
// import {ShipmentGroupsListComponent} from "../components/shipment-management/shipment-groups-list/shipment-groups-list.component";
// import {ShipmentListInGroupComponent} from "../components/shipment-management/shipment-list-in-group/shipment-list-in-group.component";
import {UserManagementConsoleComponent} from "../components/user-management-console/user-management-console.component";
import {UsersListComponent} from "../components/user-management-console/users-list/users-list.component";
const routes: Routes = [
  {path:'', pathMatch:'full', redirectTo:'dashboard'}

];

const childRoutes: Routes = [
  {path:'dashboard', component:DashboardComponent,
    children:[
      {path: '', redirectTo: 'shipmentStatus', pathMatch: 'full'},
      {path: 'shipmentStatus', component: ShipmentStatusComponent,
        children:[
          // {path: '', redirectTo: 'shipmentTable', pathMatch: 'full'},
           {path: 'selectedVehicle', component: ManualShipmentStatusComponent}
      ]},
      // {path: 'shipmentDetails', component: ShipmentDetailsComponent}
      {path: 'deviceDashboard', component: DeviceDashboard},
      {path: 'newShipment', component: NewShipment},
      {path: 'addOrganisation', component: AddOrganisationComponent},
      {path: 'settingsComponent', component: SettingsComponentComponent},
      {path: 'manageOrganisation', component: ManageOrganisationComponent},
      // {path: 'userManagementComponent', component: UserManagementComponent},
      {path: 'shipmentManagementComponent', component: ShipmentManagementComponent},
      // {path: 'shipmentGroupsList', component: ShipmentGroupsListComponent},
      // {path: 'editShipmentGroup', component: EditShipmentGroupsComponent},
      // {path: 'shipmentListInGroup', component: ShipmentListInGroupComponent},
      {path: 'userManagementConsole', component: UserManagementConsoleComponent},
      {path: 'usersList', component: UsersListComponent},
      // {path: 'userManagementConsoleWeb', component: UsermanagementWebViewComponent},
      ]
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    RouterModule.forChild(childRoutes)
  ],
  exports: [RouterModule]
})
export class RouterModuleRoutingModule { }



