import { Component, OnInit } from '@angular/core';
import {LocalStorageService} from "angular-2-local-storage";
import {Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {ManageOrganisationService} from "../../services/manage-organisation-services";
import {MdSnackBarConfig, MdSnackBar} from "@angular/material";

@Component({
  selector: 'app-add-organisation',
  templateUrl: './add-organisation.component.html',
  styleUrls: ['./add-organisation.component.css']
})
export class AddOrganisationComponent implements OnInit {
  public unSub_customerDetails:any=null;
  public customerName:string='';
  public currentOrgId:string='';
  public orgName:string='';
  public userOrgId:string='';
  public orgType:string='';
  public newOrgUuid:string='';
  public customerMobile:string='';
  public userDetails :any=null;

  private unsub_createOrgResponse: any = null;
  private unsub_switchOrgToUserResponse: any = null;

  constructor(private localStorageService: LocalStorageService,private _router:Router,private _store:Store<any>,private _manageOrganisationServices: ManageOrganisationService,public snackBar: MdSnackBar) {

    this._store.dispatch({type: "TITLE",payload:'Add Organisation'});// set the header name

    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value => {
      console.log(value);
      if(value==null){
        return;
      }

      this.currentOrgId=value['customer']['orgId'];
      this.customerName=value['customer']['name'];

    });

    this.unsub_createOrgResponse = _store.select('createOrgRes').subscribe(value => {
      if(value == null)
      {
        return;
      }
      console.log('create organisation response in component');
      console.log(value);

      if(value['error'] === '')
      {
        console.log('create organisation response in component');
        console.log(value);
        this.openSnackBar('Organisation added successfully!');
        this.newOrgUuid = value['res']['organisation.created']['uuid'];
        console.log(this.newOrgUuid);

        this._manageOrganisationServices.switchOrganisations.emit(this.newOrgUuid);

      }else{
        this.openSnackBar(value['errorMsg']);
      }
    });

    this.unsub_switchOrgToUserResponse = _store.select('switchOrganisationResp').subscribe(value => {
      if(value == null)
      {
        return;
      }
      console.log('switch organisation response in component');
      console.log(value);

      if(value['error'] === '')
      {
        this.localStorageService.set("AUTH_TOKEN",value['res']['token']);

        // let customerData=this.customerDetail(value['res']['token']);
        let customerData = this.customerDetail(value['res']['token']);
        console.log(customerData);
        let path='http://'+window.location.hostname+':'+window.location.port;
        console.log(path+"/transporter");
        if(customerData['details']['orgType'] =='TRANSPORTER'){
          window.location.replace(path+"/transporter");
        }else if(customerData['details']['orgType'] =='FLEET_OWNER'){
          window.location.replace(path+"/tracknet/vtsdashboard");
        }else if(customerData['details']['orgType'] =='FLEET_OWNER_AGENT'){
          // window.location.replace(path+"/tracknet/vtsdashboard");
        }
      }else{
        this.openSnackBar(value['errorMsg']);
      }
    });

  }

  ngOnInit() {
  }

  ngOnDestroy()
  {
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();
  }



  addNewOrganisation()
  {
    let obj = {
      orgId: this.userOrgId,
      organisationName: this.orgName,
      type: this.orgType
    }

    console.log(obj);
    this._manageOrganisationServices.createOrg.emit(obj);
  }

  customerDetail(token){
    console.log(token);
    let userDetails ={};
    this.userDetails =null;

    let base64Url = token.split('.')[0];
    let base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['userType']=JSON.parse(window.atob(base64));

    base64Url = token.split('.')[1];
    base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['details']=JSON.parse(window.atob(base64));
    // userDetails['details']['id'] =userDetails['details']['userId'];

    this.userDetails =userDetails;
    console.log(this.userDetails);
    return userDetails;

  }

  goToDashboard()
  {
       this._router.navigate(['dashboard']);
  }

  openSnackBar(message: string){
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  3000});
  }

}



