import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {Store} from "@ngrx/store";
import {ShipmentMgmtService} from "../../services/shipment-mgmt-services";
import {MdSnackBarConfig, MdSnackBar} from "@angular/material";

@Component({
  selector: 'app-shipment-management',
  templateUrl: './shipment-management.component.html',
  styleUrls: ['./shipment-management.component.css']
})
export class ShipmentManagementComponent implements OnInit {
  private unSub_customerDetails: any = null;
  private isSpinner: boolean = false;
  private unSub_shipmentGroupList: any = null;
  private unSub_createGroupRes: any = null;
  public isShowAddNewGroupDialog:boolean=false;
  public shipmentGrpList:any=[];
  public groupName:string='';
  public customerDetails:any=null;

  ngOnDestroy(){
    if (typeof this.unSub_customerDetails != 'undefined' && this.unSub_customerDetails != null && this.unSub_customerDetails != undefined)
      this.unSub_customerDetails.unsubscribe();

    if (typeof this.unSub_shipmentGroupList != 'undefined' && this.unSub_shipmentGroupList != null && this.unSub_shipmentGroupList != undefined)
      this.unSub_shipmentGroupList.unsubscribe();

    if (typeof this.unSub_createGroupRes != 'undefined' && this.unSub_createGroupRes != null && this.unSub_createGroupRes != undefined)
      this.unSub_createGroupRes.unsubscribe();
  }


  constructor(public _router:Router,private _store:Store<any>,public snackBar: MdSnackBar,private shipmentMgmtService:ShipmentMgmtService,){
    this.unSub_customerDetails = _store.select('customerDetails').subscribe(value =>{
      console.log(value);
      if(value==null){
        return;
      }
      console.log(value['customer']['orgId']);
      this.customerDetails = value['customer'];
      this.isSpinner=true;
      this.shipmentMgmtService.getAllShipmentGroupsList.emit(value['customer']['orgId']);
    });


    this.unSub_shipmentGroupList = _store.select('shipmentGroupList').subscribe(value =>{
      console.log(value);
      if(value == null){
        return;
      }
      this.isSpinner=false;
      if(value['error'] ==''){
        console.log(value['res']);
        this.shipmentGrpList =value['res'];
      }else{
        this.openSnackBar(value['errorMsg']);
      }

    });

    // when new shipment group created
    this.unSub_createGroupRes = this._store.select('createGroupRes').subscribe(value =>{
      console.log(value);
      if(value == null){
        return;
      }

      if(value['error'] ==''){
        console.log(value['res']);
        this.openSnackBar("Group created successfully");
        if(this.customerDetails['orgId'] != null ){
          this.isSpinner=true;
          this.groupName ='';
          this.shipmentMgmtService.getAllShipmentGroupsList.emit(this.customerDetails['orgId']);
        }

      }else{
        this.openSnackBar(value['errorMsg']);
      }

    });



  }//constructor close

  ngOnInit() {
  }

  // goToGroupedShipmentList() {
  //   this._router.navigate(['dashboard/userManagementComponent']);
  // }


  createNewGroup()
  {
    if(this.checkValidation()){
      this.isShowAddNewGroupDialog =false;
      let grpObj={
        orgId : this.customerDetails['orgId'],
        groupName : this.groupName,
        groupType: "shipmentGroup"
      }
      console.log(grpObj);
      this.shipmentMgmtService.createNewShipmentGroup.emit(grpObj);
    }

  }

  checkValidation(){
    if(this.groupName == ""){
      this.openSnackBar("Please fill Group name");
      return false;
    }

    if(this.customerDetails['orgId'] ==null){
      this.openSnackBar("Please refresh page or login again");
      return false;
    }

    return true;
  }

  openSnackBar(message: string) {
    this.snackBar.open(message, '', <MdSnackBarConfig>{duration :  4000});
  }
}



