import { Component, OnInit,Inject} from '@angular/core';
import {Router, ActivatedRoute} from "@angular/router";
import {MdDialog, MdDialogRef, MD_DIALOG_DATA} from '@angular/material';
import {ShipmentServices} from "../../services/shipment-services";
import { ManualShipmentServices } from "../../services/manual-shipment-service";
import {Store} from "@ngrx/store";

@Component({
  selector: 'app-manual-shipment-status',
  templateUrl: './manual-shipment-status.component.html',
  styleUrls: ['./manual-shipment-status.component.css']
})
export class ManualShipmentStatusComponent implements OnInit {
public vehicleState:any='';
public vehicleStatus:any='';
public unSub_shipmentDetail= null;
public isExtended :boolean = false;
public selectedShipment :any = {};
public movLegLength:any;
public tillTime:any;
public firstUpdateLocation:any;
public secondUpdateLocation:any;
public tripId;
public reportingTime:any=null;
public deptatureTime:any=null;
public pickupLocation:any;
public deliveryLocation:any;

public isSpinner=true;
public updateTime:any;
public currentLocationName:any;
public movementLegs:any[]=[];
public isShowLocation:boolean=true;

/************* */
public forEditPlace;
public trackingType="manual";
public isConfirmPickupDialog:boolean=false;
public isConfirmDeliveryDialog:boolean=false;
public isConfirmUpdateLocationDialog=false;
public vehicleId:string;
public firstTime;
public validationErrorMgs=null;


  constructor(public _router:Router,public dialog: MdDialog,public shipmentServices:ShipmentServices,private _store:Store<any>,public manualShipment:ManualShipmentServices)
  {

        //TODO unsubscribe shipmentDetail
        this.unSub_shipmentDetail=this._store.select('shipmentDetail').subscribe(value => {
           if(value!=null){
             if(value['error']=='') {
              this.isShowLocation=true;
              this.isSpinner=false;
              this.selectedShipment=value['res'];
              this.tripId=this.selectedShipment['vts']['tripId'];
              this.vehicleId=this.selectedShipment['vts']['vehicleId'];
              this.vehicleStatus=this.selectedShipment.vts.state;
              this.pickupLocation=this.selectedShipment['currentShipment']['startLocation']['freightLocation']['name'];
              this.deliveryLocation=this.selectedShipment['currentShipment']['endLocation']['freightLocation']['name'];              
              if(this.selectedShipment.movementLegs!=null){
                this.movementLegs=this.selectedShipment.movementLegs.reverse();
                this.movLegLength=this.selectedShipment.movementLegs.length;
                for (var i = 0; i <this.movementLegs.length; i++) {
                  this.movementLegs[i]['_date'] = this.tsToDateTime(this.movementLegs[i].startTime);
                }
              // console.log(this.movementLegs);
              }
              this.tillTime=this.selectedShipment.currentShipment.endLocation.tillTime;
               if(this.vehicleStatus=='EnrouteForPickUp'){
                 this.reportingTime=null; 
                 this.deptatureTime=null; 
               }
               if(this.vehicleStatus=='AtPickUpPoint'){
                 this.deptatureTime=null;
                 this.selectedShipment['currentShipment']['startLocation']['_sinceTime']=this.msToTime(new Date().getTime()-this.selectedShipment['currentShipment']['startLocation']['fromTime']);
                 this.reportingTime=new Date(this.selectedShipment['currentShipment']['startLocation']['fromTime']).toDateString()+' '+new Date(this.selectedShipment['currentShipment']['startLocation']['fromTime']).toTimeString();
               }
               if(this.vehicleStatus=='EnrouteForDestination'){
                this.reportingTime=null;
                this.deptatureTime=null;
                 this.selectedShipment['currentShipment']['startLocation']['_endTime']=this.tsToDateTime(this.selectedShipment['currentShipment']['planedEndTime']);                                  
                 this.selectedShipment['currentShipment']['startLocation']['_loadTime']=this.msToTime(this.selectedShipment['currentShipment']['startLocation']['tillTime']-this.selectedShipment['currentShipment']['startLocation']['fromTime']);
                 this.selectedShipment['currentShipment']['startLocation']['_dateTime']=this.tsToDateTime(this.selectedShipment['currentShipment']['startLocation']['fromTime']);                 
               }
               if(this.vehicleStatus=='AtDestination'){
                 this.reportingTime=new Date(this.selectedShipment['currentShipment']['endLocation']['fromTime']).toDateString()+' '+new Date(this.selectedShipment['currentShipment']['endLocation']['fromTime']).toTimeString();
                 this.selectedShipment['currentShipment']['endLocation']['_sinceTime']=this.msToTime(new Date().getTime()-this.selectedShipment['currentShipment']['endLocation']['fromTime']);                 
                 this.selectedShipment['currentShipment']['endLocation']['_unLoadTime']=this.msToTime( this.selectedShipment['currentShipment']['endLocation']['tillTime']-this.selectedShipment['currentShipment']['endLocation']['fromTime']);                 
                 this.selectedShipment['currentShipment']['startLocation']['_loadTime']=this.msToTime(this.selectedShipment['currentShipment']['startLocation']['tillTime']-this.selectedShipment['currentShipment']['startLocation']['fromTime']);
                 this.selectedShipment['currentShipment']['startLocation']['_dateTime']=this.tsToDateTime(this.selectedShipment['currentShipment']['startLocation']['fromTime']);                 
                 if(this.tillTime==null || this.tillTime==undefined || this.tillTime==""){
                  this.selectedShipment['currentShipment']['endLocation']['_destDateTime']=this.tsToDateTime(this.selectedShipment['currentShipment']['planedEndTime']);
                 }
                 if(this.tillTime!=null || this.tillTime!=undefined || this.tillTime!=""){
                  this.selectedShipment['currentShipment']['endLocation']['_destDateTime']=this.tsToDateTime(this.selectedShipment['currentShipment']['endLocation']['tillTime']);
                 }
               }
              if (this.selectedShipment['currentShipment'] != null) {
                this.selectedShipment['currentShipment']['_transiteTime'] = this.msToTime(this.selectedShipment['currentShipment']['planedEndTime'] - this.selectedShipment['currentShipment']['planedStartTime']);
                console.log(this.selectedShipment['currentShipment']['_transiteTime']);
              }
             }
             else
             {

             }
           }
         });

   }

  ngOnInit() {

  }
  /********************************************************** */

  goToConfirmPickupView(){
    this.validationErrorMgs=null;
    this.forEditPlace  ="Confirm Pickup";
    this.isConfirmPickupDialog =true;
  }
  confirmPickup()
  {
    if(this.vehicleStatus=='EnrouteForPickUp'){
      if(!this.checkRepoValid()){
        return;
      }
      if(this.deptatureTime!=null){
        if(!this.checkDepaValid()){
          return;
        }
      }
    }
    if(this.vehicleStatus=='AtPickUpPoint'){
      if(!this.checkRepoValid()){
        return;
      }
      if(!this.checkDepaValid()){
        return;
      }
    }
      let repoTime:any;
      if(this.reportingTime==null || this.reportingTime==undefined || this.reportingTime==''){
        repoTime=null;
      }
      else{
        repoTime=new Date(this.reportingTime).getTime();
      }
      let deptTime:any;
      if(this.deptatureTime==null || this.deptatureTime==undefined || this.deptatureTime==''){
        deptTime=null;
      }
      else{
        deptTime=new Date(this.deptatureTime).getTime();
      }
      //console.log(this.depTime);
      let obj={
        "vehicleId": this.vehicleId,
        "type": "user.add.missing.pickup",
        "tripId":this.tripId,
        "startTime":repoTime,
        "endTime":deptTime,
        "trackingType":this.trackingType,
        "deviceImei": null,
        "locationInfo": null,
        "vehicleStateInfo": null
        }
        console.log(obj);
        this.manualShipment.pickup.emit(obj);
        this.isConfirmPickupDialog=false;
        this.isSpinner=true;
        setTimeout(()=>{
          this.shipmentServices.getShipmentDetail.emit(this.selectedShipment.currentShipment.shipmentId);
        },500);
  }
  goToConfirmDeliveryView(){
    this.validationErrorMgs=null;
    this.forEditPlace  ="Confirm Delivery";
    this.isConfirmDeliveryDialog =true;
  }
  goToupdateLocationView(){
    this.forEditPlace  ="Add Location";
    this.isConfirmUpdateLocationDialog =true;
  }

  addLocation()
  {
    if(this.updateTime == null ){
      this.validationErrorMgs = "Update Time should not be blank";
      return;
    }
    let todayDate = new Date().getTime();
    if (new Date(this.updateTime).getTime() > todayDate) {
      this.validationErrorMgs = "Update Time should not be greater then current date";
      return;
    }

     let obj={
      "vehicleId": null,
      "type": "add.stop",
      "tripId":this.tripId,
      "startTime": new Date(this.updateTime).getTime(),
      "endTime": null,
      "deviceImei": null,
      "trackingType":this.trackingType,
      "locationInfo":
      {
      "latitude": 28.4592693,
      "longitude": 77.07241920000001,
      "speed": 0.0,
      "time": null,
      "imei": " ",
      "vehicleId": " ",
      "address": this.currentLocationName,
      "lngLat": [0.0]
      },
      "vehicleStateInfo": null
    };
    console.log(obj);
    this.manualShipment.addStop.emit(obj);
    this.isConfirmUpdateLocationDialog=false;
    this.isSpinner=true;
    setTimeout(()=>{
      this.shipmentServices.getShipmentDetail.emit(this.selectedShipment.currentShipment.shipmentId);
    },500);
  }

  checkRepoValid(){
      if(this.reportingTime == null ){
        this.validationErrorMgs = "Reporting Time should not be blank";
        return false;
      }
      let todayDate = new Date().getTime();
      if (new Date(this.reportingTime).getTime() > todayDate) {
        this.validationErrorMgs = "Reporting Time should not be greater then current date";
        return false;
      }
      
      return true;
  }// close ckeck valid
  checkDepaValid(){
    if(this.deptatureTime == null){
      this.validationErrorMgs = "Departure Time should not be blank";
      return false;
    }
    
    if(new Date(this.deptatureTime).getTime()<new Date(this.reportingTime).getTime()){
      this.validationErrorMgs = "Departure Time should not be less then Reporting Time";
      return false;
    }
    let todayDate = new Date().getTime();      
    if (new Date(this.deptatureTime).getTime() > todayDate) {
      this.validationErrorMgs = "Departure Time should not be greater then current date";
      return false;
    }
    return true;
  }

  confirmDelivery()
  {
    if(this.vehicleStatus=='EnrouteForDestination'){
      if(!this.checkRepoValid()){
        return;
      }
      if(this.deptatureTime!=null){
        if(!this.checkDepaValid()){
          return;
        }
      }
    }
    if(this.vehicleStatus=='AtDestination'){
      if(!this.checkRepoValid()){
        return;
      }
      if(!this.checkDepaValid()){
        return;
      }
    }
    let repoTime:any;
    if(this.reportingTime===null){
      repoTime=null;
    }
    else{
      repoTime=new Date(this.reportingTime).getTime();
    }
    let deptTime:any;
    if(this.deptatureTime===null){
      deptTime=null;
    }
    else{
      deptTime=new Date(this.deptatureTime).getTime();
    }
    //console.log(this.depTime);
    let obj={
      "vehicleId":this.vehicleId,
      "type": "user.add.missing.delivery",
      "tripId":this.tripId,
      "startTime":repoTime,
      "endTime":deptTime,
      "trackingType":this.trackingType,
      "deviceImei": null,
      "locationInfo": null,
      "vehicleStateInfo": null
      }
      console.log(obj);
      this.manualShipment.delivery.emit(obj);
      this.isConfirmDeliveryDialog=false;
      this.isSpinner=true;
      setTimeout(()=>{
        this.shipmentServices.getShipmentDetail.emit(this.selectedShipment.currentShipment.shipmentId);
      },1000);
  }
  tsToDate(timeStamp){
    let date:any;
    date=new Date(timeStamp).toDateString();
    // Sun Jan 18 1970
    let dateTime=date.substr(8,2)+'-'+date.substr(4,3)+' '+date.substr(11,4);
    console.log(dateTime);
    return dateTime;
  }

  tsToDateTime(timeStamp){
    let date:any;
    let time:any;
    date=new Date(timeStamp).toDateString();
    time=new Date(timeStamp).toTimeString().substr(0,5);
    let x;
    if(time.substr(0,2)>=12){
      x= time.substr(0,2)%12+time.substr(2,3)+ ' PM';
    }
    else{
      x= time.substr(0,5) + ' AM';
    }
    // Sun Jan 18 1970
    let dateTime=date.substr(8,2)+'-'+date.substr(4,3)+' '+date.substr(11,4)+', at '+x;
    console.log(dateTime);
    return dateTime;
  }

  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes>0){
      return totalDuration=totalDuration+minutes+"min";
    }

    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }
    
  }//close methods msToTime

  ngOnDestroy() {
    if (typeof this.unSub_shipmentDetail != 'undefined' && this.unSub_shipmentDetail != null && this.unSub_shipmentDetail != undefined)
    this.unSub_shipmentDetail.unsubscribe();

    // this.isShowLocation=true;


  }

}





