import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
import {LocalStorageService} from "angular-2-local-storage";
import {BASE_IP,TEST_BASE_IP, paths} from "../../environments/environment";

@Injectable()
export class AuthorizationServices {
  // protected basePath = 'http://35.189.162.187:8080/command';
  public defaultHeaders: Headers = new Headers();
  private token:any='';
  private unSub_token:any=null;
  public tokenAuthorization=new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorageService: LocalStorageService){
    this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');

    //************ login  ***************************************************
    this.tokenAuthorization.subscribe((token)=> {
      // const path =paths.PATH+':8094/authorize';
      // const path =paths.PATH+':8099/user/authorize';
      const path =TEST_BASE_IP.IP+'/user/authorize';
      this.checkTokenAuthorization(path,token).subscribe(response =>{
        console.log(response);
        // console.log(response['error']);

        if(response['error']=='X'){
          this.localStorageService.remove("TOKEN");
          this.localStorageService.remove("AUTH_TOKEN");
          this._store.dispatch({type: "AUTHORIZATION",payload:{valid:false,error:'X',msg:"error occur"}});

          return;
        }
        let res=response;
        if(res['status']==200){

          this.localStorageService.set("TOKEN",token);
          this._store.dispatch({type: "AUTHORIZATION", payload: {valid:true,error:'',msg:"User authorized"}});
          let customer=this.customerDetail(token);
          console.log("this is token in after valid authorization");
          console.log(customer);
          this._store.dispatch({type: "CUSTOMER_DETAILS", payload:{error:'',customer:customer}});
        }
        else{
          this.localStorageService.remove("TOKEN");
          this.localStorageService.remove("AUTH_TOKEN");
          this._store.dispatch({type: "AUTHORIZATION",payload:{valid:false,error:'X',msg:"Unauthorised user in response"}});
        }

      },err=>{
        this.localStorageService.remove("TOKEN");
        this.localStorageService.remove("AUTH_TOKEN");
        this._store.dispatch({type: "AUTHORIZATION",payload:{valid:false,error:'X',msg:err.json()}});
        // this.localStorageService.set("TOKEN",false);
        console.log(err);
        console.log(err.json());
      });
    });
  }// constructor close here

  // ***********************  token authorization**********************
  public checkTokenAuthorization(path,token){

    // let queryParameters = new URLSearchParams();
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken);
    console.log(path);

    // console.log(headers);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });


    return this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      console.log(response);
      if(response.status==200){
        return response.json();
      }else{
        console.log("not 200");
        return {error:'X'};
      }

      // let resObj={
      //   name : "dummy response for test",
      //   status : 200,
      //   valid : true,
      //   error : ''
      // }

      // return resObj;
    });
  }

public customerDetail(token){
  // console.log(token);
    var base64Url = token.split('.')[1];
    var base64 = base64Url.replace('-', '+').replace('_', '/');
    return JSON.parse(window.atob(base64));
}

}// class close




