import {Component, OnInit, Input, EventEmitter,NgZone} from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import {LocalStorageService} from "angular-2-local-storage";
import {Router} from "@angular/router";
import {paths} from "../../../environments/environment";
import {Store} from "@ngrx/store";
import {LogoutServices} from "../../services/logout-service";

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})

export class UserProfile implements OnInit {
  @Input() view:any;

  public unSub_customerDetails:any=null;
  public customerName:string='';
  public customerMobile:string='';
  public currentOrgId:string='';
  public userDetails :any=null;
  public isShowGodFeatures:boolean =false;
  public viewType ='';
  public isOrgList =false;
  public authToken =null;
  public defaultHeaders: Headers = new Headers();
  public orgList :any[]=[];
  public orgListNew :any[]=[];
  public search :any='';

  ngOnDestroy(){

  }

  constructor(private  zone  :NgZone,protected http: Http,private localStorageService: LocalStorageService,private logoutService:LogoutServices,private _router:Router,private _store:Store<any>) {
    this.defaultHeaders.append('Access-Control-Allow-Origin', '*');
    this.defaultHeaders.append('Access-Control-Allow-Methods', 'GET,POST, OPTIONS, PUT, PATCH, DELETE');

    let auth_token = this.localStorageService.get("AUTH_TOKEN");
    console.log(auth_token);

    if(auth_token !=null){
      this.authToken =auth_token;
      this.refreshUser();
    }
  }// constructor close

  refreshUser(){
    console.log(this.authToken);

    this.customerDetail(this.authToken);
    // this.organizationServices.getOrgList.emit(auth_token);
    this.getOrganization(this.authToken);
  }

  ngOnInit() {
    this.viewType = this.view;
    // console.log(this.view);
  }

  customerDetail(token){
    let userDetails ={};
    this.userDetails =null;

    let base64Url = token.split('.')[0];
    let base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['userType']=JSON.parse(window.atob(base64));

    base64Url = token.split('.')[1];
    base64 = base64Url.replace('-', '+').replace('_', '/');
    userDetails['details']=JSON.parse(window.atob(base64));
    userDetails['details']['id'] =userDetails['details']['userId'];

    this.userDetails =userDetails;
    this.customerName = userDetails['details']['name'];
    this.customerMobile=userDetails['details']['mobileNumber'];
    this.currentOrgId=userDetails['details']['orgId'];

    if(userDetails['details']['isGod'] != null || userDetails['details']['isGod'] != undefined)
    {
      console.log("isGod not null or undefined");
      console.log(userDetails['details']['isGod']);
      if(userDetails['details']['isGod'] == true)
      {

        this.isShowGodFeatures=true;
      }
    }
    this.localStorageService.set("ORGID",this.currentOrgId);
    console.log(this.userDetails);
    this._store.dispatch({type: "CUSTOMER_DETAILS", payload:{customer:this.userDetails}});
  }

  logout(){
    this.logoutService.logout();
  }

  switchOrg(currentOrg, isGodOrg){
    if(!isGodOrg) {
      console.log(currentOrg);
      console.log(this.userDetails);
      let path = paths.SWITCH_ORG_PATH + currentOrg['uuid'];
      let headers = new Headers(this.defaultHeaders.toJSON());
      headers.set('Content-Type', 'application/json');
      let bearerToken = "Bearer " + this.authToken;
      headers.set('authorization', bearerToken);

      let requestOptions: RequestOptionsArgs = new RequestOptions({
        method: RequestMethod.Get,
        headers: headers,
        // body: Id,
      });

      console.log(path);
      console.log(bearerToken);
      this.http.request(path, requestOptions).map((response: Response) => {
        // console.log(response.status);
        // console.log(response);
        return response.json();
      }).subscribe((response) => {
        console.log(response);
        if (response['status'] >= 200 && response['status'] < 300) {
          if (this.userDetails['details']['orgType'] == currentOrg['type']) {
            // this.localStorageService.clearAll();
            this.localStorageService.remove("AUTH_TOKEN");

            this.localStorageService.set("AUTH_TOKEN", response['token']);
            this.authToken = this.localStorageService.get("AUTH_TOKEN");
            this.refreshUser();

            this.zone.runOutsideAngular(() => {
              location.reload();
            });
          } else {
            // this.localStorageService.clearAll();
            this.localStorageService.remove("AUTH_TOKEN");
            this.localStorageService.set("AUTH_TOKEN", response['token']);

            let path = 'http://' + window.location.hostname + ':' + window.location.port;
            console.log(path + "/transporter");

            if (currentOrg['type'] == 'TRANSPORTER') {
              window.location.replace(path + "/transporter");
            } else if (currentOrg['type'] == 'FLEET_OWNER') {
              window.location.replace(path + "/tracknet/vtsdashboard");
            } else if (currentOrg['type'] == 'FLEET_OWNER_AGENT') {
              // window.location.replace(path+"/tracknet/vtsdashboard");
            }

            //@todo  add only if org type
            // this.zone.runOutsideAngular(() => {
            //   location.reload();
            // });
          }
        } else {
          console.log("error");
        }
      }, err => {
        console.log(err);
        console.log(err.json());
      });
    }

    if(isGodOrg)
    {
      console.log("is God User user");
      console.log(currentOrg);
      console.log(this.userDetails);
      let path = paths.SWITCH_ORG_PATH + currentOrg['uuid'];
      let headers = new Headers(this.defaultHeaders.toJSON());
      headers.set('Content-Type', 'application/json');
      let bearerToken = "Bearer " + this.authToken;
      headers.set('authorization', bearerToken);

      let requestOptions: RequestOptionsArgs = new RequestOptions({
        method: RequestMethod.Get,
        headers: headers,
        // body: Id,
      });

      console.log(path);
      console.log(bearerToken);
      this.http.request(path, requestOptions).map((response: Response) => {
        // console.log(response.status);
        // console.log(response);
        return response.json();
      }).subscribe((response) => {
        console.log(response);
        if (response['status'] >= 200 && response['status'] < 300) {
          if (this.userDetails['details']['orgType'] == currentOrg['type']) {
            // this.localStorageService.clearAll();
            this.localStorageService.remove("AUTH_TOKEN");
            this.localStorageService.set("AUTH_TOKEN", response['token']);
            this.authToken = this.localStorageService.get("AUTH_TOKEN");
            this.refreshUser();

            this.zone.runOutsideAngular(() => {
              location.reload();
            });
          } else {
            // this.localStorageService.clearAll();
            this.localStorageService.remove("AUTH_TOKEN");
            this.localStorageService.set("AUTH_TOKEN", response['token']);

            let path = 'http://' + window.location.hostname + ':' + window.location.port;
            console.log(path + "/transporter");

            if (currentOrg['type'] == 'TRANSPORTER') {
              window.location.replace(path + "/transporter");
            } else if (currentOrg['type'] == 'FLEET_OWNER') {
              window.location.replace(path + "/tracknet/vtsdashboard");
            } else if (currentOrg['type'] == 'FLEET_OWNER_AGENT') {
              // window.location.replace(path+"/tracknet/vtsdashboard");
            }

            //@todo  add only if org type
            // this.zone.runOutsideAngular(() => {
            //   location.reload();
            // });
          }
        } else {
          console.log("error");
        }
      }, err => {
        console.log(err);
        console.log(err.json());
      });
    }
  } //close switch org

  //get organization
  public getOrganization(token){
    this.orgList =[];
    let path =paths.ORGANIZATION_LIST_PATH
    let headers = new Headers(this.defaultHeaders.toJSON());
    headers.set('Content-Type', 'application/json');
    let bearerToken="Bearer "+token;
    headers.set('authorization', bearerToken);

    let requestOptions: RequestOptionsArgs = new RequestOptions({
      method: RequestMethod.Get,
      headers: headers,
      // body: Id,
    });

     this.http.request(path, requestOptions).map((response: Response) => {
      // console.log(response.status);
      // console.log(response);
       return response.json();
    }).subscribe((response)=>{
      console.log(response);
      if(response['status'] >=200 && response['status'] <300){
        this.orgList =response['list'];
        if(this.isShowGodFeatures){
          this.orgListNew.push({uuid:'FRETRON_GOD_FO',type:'FLEET_OWNER',isGodOrg:true,organisationName:'SUPER USER FLEET OWNER',orgId:null});
          this.orgListNew.push({uuid:'FRETRON_GOD_TRANSPORTER',type:'TRANSPORTER',isGodOrg:true,organisationName:'SUPER USER TRANSPORTER',orgId:null})
        }
        for (let i = 0;i<this.orgList.length ;i++) {
          this.orgList[i]['isGodOrg']=false;
          this.orgListNew.push(this.orgList[i]);
        }
        console.log(this.orgListNew);
      }else{
              console.log(response['error']);
      }
    },err=>{
      console.log(err);
      console.log(err.json());
    });
  }

  goToAddOrganisation()
  {
    this._router.navigate(['dashboard/addOrganisation']);
  }

}// class close



