import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";

@Component({
  selector: 'app-manage-organisation',
  templateUrl: './manage-organisation.component.html',
  styleUrls: ['./manage-organisation.component.css']
})
export class ManageOrganisationComponent implements OnInit {

  constructor(public _router:Router) { }

  ngOnInit() {
  }

  goToShipmentGroupsManagement()
  {
    this._router.navigate(['dashboard/shipmentManagementComponent']);
  }

  goToUserManagement()
  {
    // this._router.navigate(['dashboard/userManagementComponent']);
    this._router.navigate(['dashboard/userManagementConsole']);
  }

}



