import { Component,Inject, Injectable, Optional,EventEmitter,Output }                      from '@angular/core';
import { Http, Headers, URLSearchParams }                    from '@angular/http';
import { RequestMethod, RequestOptions, RequestOptionsArgs } from '@angular/http';
import { Response, ResponseContentType }                     from '@angular/http';

import { Observable }                                        from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Store} from "@ngrx/store";
// import {ADDSTOP,PICKUP,DELIVERY} from "./path-services";
import { LocalStorageService } from "angular-2-local-storage";
import { paths } from "../../environments/environment";
import {ShipmentServices} from "../services/shipment-services";


// import {KEY} from "./config-services"

@Injectable()
export class ManualShipmentServices {
  public defaultHeaders: Headers = new Headers();
  private token:any='';
   
   
  public pickup=new EventEmitter();
  public addStop=new EventEmitter();
  public delivery=new EventEmitter();

  ngOnDestroy(){

  }

  constructor(protected http: Http,private _store:Store<any>,private localStorgeService:LocalStorageService,public shipmentServices:ShipmentServices ){
     /***************get shipment list ***************/
     this.token=this.localStorgeService.get("AUTH_TOKEN");
     

     /***************set pickup ***************/
     this.pickup.subscribe((pickUp)=> {
      let path =paths.SHIPMENT_PATH+'/missing/pickup';
      console.log(path);
      this.setPickup(path,pickUp).subscribe(response =>{
        console.log(response);
        let res=response;

      },err=>{
        console.log(err.json());
      });
    });

     /***************set addStop ***************/
     this.addStop.subscribe((addstop)=> {
      let path=paths.SHIPMENT_PATH+'/addstop';
      this.setAddStop(path,addstop).subscribe(response =>{
        console.log(response);
        let res=response;
      },err=>{
        console.log(err.json());
      });
    });

     /***************set delivery ***************/
     this.delivery.subscribe((deliveryy)=> {
      let path = paths.SHIPMENT_PATH+'/missing/delivery';
      this.setDelivery(path,deliveryy).subscribe(response =>{
        console.log(response);
        let res=response;

      },err=>{
        console.log(err.json());
      });
    });
     
     
  }// constructor close here

// ***********************  get shipmentId list **********************
public getIdList(path){
  let headers = new Headers(this.defaultHeaders.toJSON());
  let bearerToken="Bearer "+this.token;
  headers.set('authorization', bearerToken); 
  headers.set('Content-Type', 'application/json');
  let requestOptions: RequestOptionsArgs = new RequestOptions({
    method: RequestMethod.Get,
    headers: headers,
  });

console.log(this.http);
  return this.http.request(path, requestOptions).map((response: Response) => {
    console.log(response);
    this.shipmentServices.getShipmentList.emit();
      return response.json();
  });
}

// ***********************  add Stop **********************
public setAddStop(path,value){
  let headers = new Headers(this.defaultHeaders.toJSON());
  headers.set('Content-Type', 'application/json');
  let bearerToken="Bearer "+this.token;
  headers.set('authorization', bearerToken);
  let requestOptions: RequestOptionsArgs = new RequestOptions({
    method: RequestMethod.Post,
    headers: headers,
    body:value
  });

console.log(this.http);
  return this.http.request(path, requestOptions).map((response: Response) => {
    console.log(response);
    setTimeout(()=>{
      this.shipmentServices.getShipmentList.emit();
    },1500);
    
      return response.json();
  });
}
// ***********************  pickUp **********************
public setPickup(path,value){
  console.log(path);
  let headers = new Headers(this.defaultHeaders.toJSON());
  headers.set('Content-Type', 'application/json');
  let bearerToken="Bearer "+this.token;
  headers.set('authorization', bearerToken);
  let requestOptions: RequestOptionsArgs = new RequestOptions({
    method: RequestMethod.Post,
    headers: headers,
    body:value
  });

console.log(this.http);
  return this.http.request(path, requestOptions).map((response: Response) => {
    console.log(response);
    setTimeout(()=>{
      this.shipmentServices.getShipmentList.emit();
    },1500);
      return response.json();
  });
}
// ***********************  delivery **********************
public setDelivery(path,value){
  let headers = new Headers(this.defaultHeaders.toJSON());
  headers.set('Content-Type', 'application/json');
  let bearerToken="Bearer "+this.token;
  headers.set('authorization', bearerToken);
  let requestOptions: RequestOptionsArgs = new RequestOptions({
    method: RequestMethod.Post,
    headers: headers,
    body:value
  });

console.log(this.http);
  return this.http.request(path, requestOptions).map((response: Response) => {
    console.log(response);
    setTimeout(()=>{
      this.shipmentServices.getShipmentList.emit();
    },1500);
      return response.json();
  });
}
 
}// class close




