import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {DeviceServices} from "../../services/device-services";
import {ShipmentServices} from "../../services/shipment-services";
import {Store} from "@ngrx/store";
import { isNullOrUndefined } from "util";
import {MdDialog, MdDialogRef, MD_DIALOG_DATA, MdSnackBar, MdSnackBarConfig} from '@angular/material';
import {ConfirmDeviceReturnComponent} from "../../components/dialog/confirm-device-return/confirm-device-return.component";
import { AssignVehicleComponent } from '../../components/dialog/assign-vehicle/assign-vehicle.component';
import { MarkAsRequiredComponent } from '../../components/dialog/mark-as-required/mark-as-required.component';

@Component({
  selector: 'app-device-dashboard',
  templateUrl: './device-dashboard.component.html',
  styleUrls: ['./device-dashboard.component.css']
})
export class DeviceDashboard implements OnInit {

  public selectedFilter = '';
  public isSpinner = false;
  public  allDeviceList:any[]=[];
  public deviceDetail:any;
  public deviceIMEI:any=null;
  public manufacturerName=null;
  public trackingType:any;
  public isShowDetails:boolean=false;
  public search:any='';
  
  public countFilterData:any ={all:0,active:0,available:0,repairing:0};
  // public  allActiveDeviceList:any[]=[];
  // public  allAvailableDeviceList:any[]=[];
  // public  allRepairingDeviceList:any[]=[];

  public unSub_allDeviceList=null;
  public unSub_addDevice=null;
  public isAddDeviceDialog:boolean=false;
  // public unSub_allActiveDeviceList=null;
  // public unSub_allAvailableDeviceList=null;
  // public unSub_allRepairingDeviceList=null;
  constructor(public snackBar: MdSnackBar,public _router:Router,public deviceServices:DeviceServices,public shipmentServices:ShipmentServices,public dialog: MdDialog,private _store:Store<any>)
  {
    this.deviceServices.allDevicesList.emit();
    this.isSpinner =true;
    this.unSub_allDeviceList=this._store.select('allDevicesList').subscribe(value => {
      console.log(value);
      if(value !=null){
        this.isSpinner =false;
        if(value['error'] ==''){
          //for Error Handaling
          this.allDeviceList = value['res'];
          console.log(this.allDeviceList);
          this.countFilterValue();
        }
        else {
          console.log(value['errorMsg']);
          this.openSnackBar(value['errorMsg']);
        }
      }
    });
    this.unSub_addDevice=this._store.select('addDevice').subscribe(value => {
      if(value !=null){
        if(value['error'] ==''){
          
          //for Error Handaling
          if (value['res']['status'] >= 200 && value['res']['status'] < 300) {
           this.openSnackBar("Device added successfully");
          }
        }
        else {
          console.log(value['errorMsg']);
          this.openSnackBar(value['errorMsg']);
        }
      }
    });
  } //constructor close

  countFilterValue(){
    this.countFilterData={all:0,active:0,available:0,repairing:0};

    this.countFilterData['all'] =this.allDeviceList.length;
    for(let i=0;i <this.allDeviceList.length;i++){
      if(this.allDeviceList[i]['device']['status'] =="active"){
        if(this.allDeviceList[i]['shipment']['shipmentState']=="Completed"){
          this.allDeviceList[i]['device']['status']='available';
          this.countFilterData['available'] =  this.countFilterData['available']+1;
        }
        else{
          this.countFilterData['active'] =this.countFilterData['active']+1;        
          if(this.allDeviceList[i]['shipment']['actualStartTime'] != null){
            let stateDuration =this.msToTime(new Date().getTime() - this.allDeviceList[i]['shipment']['actualStartTime']);
            console.log(stateDuration);
            this.allDeviceList[i]['shipment']['_stateDuration'] =stateDuration;
          }
        }
        
      }else if(this.allDeviceList[i]['device']['status'] ==null){
        this.allDeviceList[i]['device']['status']='available';
        this.countFilterData['available'] =  this.countFilterData['available']+1;
      }
      // else if(this.allDeviceList[i]['status'] =="repairing"){
      //   this.countFilterData['repairing'] =  this.countFilterData['repairing']+1;
      // }
    }
    // console.log(this.countFilterData);
  }
  showAddDevice(){
    this.deviceIMEI=null;
    this.manufacturerName=null;
    this.isAddDeviceDialog=true;
    
  }
  confirmAddDevice(){
    console.log(this.deviceIMEI);
    if (isNullOrUndefined(this.deviceIMEI) || this.deviceIMEI=='') {
      
    this.openSnackBar('please fill device IMEI ');
    return;
    }

    let obj={
      
        "imei":this.deviceIMEI,
        "mobileNumber" :null,
        "manufacturerName" :this.manufacturerName
    }
    console.log(obj)
    
    this.deviceServices.addDevice.emit(obj);
    this.isAddDeviceDialog=false;
  }
  ngOnInit() {
  }
  selectedShipmentDetail(shipment){
    console.log(shipment);
    if(!isNullOrUndefined(shipment)){
    this.trackingType=shipment.trackingType;
    let id=shipment.shipmentId;
    this.shipmentServices.getShipmentDetail.emit(id);
    this.isShowDetails=true; 
    }   
  }

  ngOnDestroy() {
    if (typeof this.unSub_allDeviceList != 'undefined' && this.unSub_allDeviceList != null && this.unSub_allDeviceList != undefined)
    this.unSub_allDeviceList.unsubscribe();
    if (typeof this.unSub_addDevice != 'undefined' && this.unSub_addDevice != null && this.unSub_addDevice != undefined)
    this.unSub_addDevice.unsubscribe();
    // if (typeof this.unSub_allAvailableDeviceList != 'undefined' && this.unSub_allAvailableDeviceList != null && this.unSub_allAvailableDeviceList != undefined)
    // this.unSub_allAvailableDeviceList.unsubscribe();
    // if (typeof this.unSub_allRepairingDeviceList != 'undefined' && this.unSub_allRepairingDeviceList != null && this.unSub_allRepairingDeviceList != undefined)
    // this.unSub_allRepairingDeviceList.unsubscribe();
    this._store.dispatch({type: "ADD_DEVICE", payload:null});
    this._store.dispatch({type: "All_DEVICES_LIST", payload:null});
    
    

  }

  openDialogConfirmDeviceReturn(device:any): void {
    let res=device;
    console.log(res);
    this._store.dispatch({type: "DEVICE_DETAILS",payload:{error:'',data:res}});
    let dialogRef = this.dialog.open(ConfirmDeviceReturnComponent);
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openDialogAssignVehicle(device:any): void {
      let res=device;
      console.log(res);
      this._store.dispatch({type: "DEVICE_DETAILS",payload:{error:'',data:res}});
      let dialogRef = this.dialog.open(AssignVehicleComponent, {
        height: '350px',width: '500px'
      });
      dialogRef.afterClosed().subscribe(result => {
        console.log(`Dialog result: ${result}`);
      });
  }

  openDialogMarkAsRequired(device:any): void {
    let res=device;
    console.log(res);
    this._store.dispatch({type: "DEVICE_DETAILS",payload:{error:'',data:res}});
    let dialogRef = this.dialog.open(MarkAsRequiredComponent, {
      height: '350px',width: '500px'
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
}


  msToTime(duration) {
    let x = duration / 1000;
    let seconds = Math.floor(x % 60);
    x /= 60;
    let minutes = Math.floor(x % 60);
    x /= 60;
    let hours = Math.floor(x % 24);
    x /= 24;
    let days = Math.floor(x);

    // console.log(days);
    let totalDuration='';

    if(days>0){
      totalDuration=days+"d ";
    }
    if(hours>0){
      totalDuration=totalDuration+hours+"hr ";
    }

    if(days >= 1){
      return totalDuration;
    }

    if(minutes > 0){
      return totalDuration=totalDuration+minutes+"min";
    }
    if(seconds>0 && totalDuration == ''){
      totalDuration=totalDuration+seconds+" sec";
    }

    if(totalDuration==''){
      return '0 sec';
    }else {
      return totalDuration;
    }

  }//close methods msToTime


  openSnackBar(message: string) {
    this.snackBar.open(message , '', <MdSnackBarConfig>{duration :  3000});
  }

}



